import { Injectable } from '@angular/core';

import { Client } from './../models/client';

@Injectable()
export class ClientsService {

  public clients: Array<Client> = [];

  public selected:Client;

  constructor() {

    let cliente1 = new Client();
    cliente1.id = "1005632";
    cliente1.name = "nome1";
    this.clients.push(cliente1);

    let cliente2 = new Client();
    cliente2.id = "1005629";
    cliente2.name = "nome2";
    this.clients.push(cliente2);

  }

  getClientById(id:string){
    this.selected = this.clients.filter(
      client => client.id === id
    )[0];

    return this.selected;

  }

}



